import Fab from '@mui/material/Fab';
import {React,useState} from "react";
import AddIcon from '@mui/icons-material/Add';
import RoomModal from "./RoomModal";
import { styled } from '@mui/material/styles';

function RoomCreate(props) {
    const [modalOpen, setModalOpen] = useState(false);

    const openModal = () => {
        setModalOpen(true); // modal open
    };

    const closeModal = () => {
        setModalOpen(false); // modal close
    };

    const addModal = (room_name, room_type) => {

        setModalOpen(false); // Modal 
    };

    const ColorFab = styled(Fab)(({ theme }) => ({
          color: theme.palette.getContrastText("#ffffff"),
          backgroundColor: "#ffffff",
          '&:hover': {
            backgroundColor: "#ffffff",
          },
        }));

    return (
        <div>
            <RoomModal open={modalOpen} close={closeModal} propFunction={addModal} header=" Room ">
            </RoomModal>

            <ColorFab color="primary" aria-label="add" onClick={openModal}>
                <AddIcon />
            </ColorFab>
        </div>
    );
}
export default RoomCreate;
