import React,{useEffect} from 'react';
import {useState} from 'react';
import '../css/Modal.css';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';

const RoomModal = (props) => {

  // open, close, Modal header   
  const { open, close, add, header } = props;

  const[roomName, setRoomName] = useState('');
  const[roomType, setRoomType] = useState('');
  const[changeNum, setChangeNum] = useState(1);

  const changeRoomName = (e) => {
    setRoomName(e.target.value);
  }

  const changeRoomType = (e) => {
    setRoomType(e.target.value);
  }

  const addEvent = () => {
    setChangeNum(changeNum+1); // add     changeNum + 1 

    props.propFunction(roomName,roomType); //   
  }

  return (
     //   openModal  .
        <div className={open ? 'openModal modal' : 'modal'}>
          {open ? (
            <section>
              <header>
                {header}
                <button className="close" onClick={close}>
                </button>
              </header>

              <div className="modal-body">
                <div className="form-group">
                    <label htmlFor="taskId" className="col-form-label"> </label>
                    <input type="text" onChange={changeRoomName} className="form-control" value={roomName} id="room_name" name="room_name"/>
                    <label htmlFor="taskId" className="col-form-label"> </label>
                     <FormControl>
                          <FormLabel id="demo-controlled-radio-buttons-group"></FormLabel>
                          <RadioGroup
                            row
                            aria-labelledby="demo-controlled-radio-buttons-group"
                            name="controlled-radio-buttons-group"
                            value={roomType}
                            onChange={changeRoomType}
                          >
                            <FormControlLabel value="" control={<Radio />} label="" />
                            <FormControlLabel value="" control={<Radio />} label="" />
                          </RadioGroup>
                        </FormControl>
                </div>
              </div>

              <footer>
                <button className="add" onClick={addEvent}>
                  add
                </button>
                <button className="close" onClick={close}>
                  close
                </button>
              </footer>

            </section>
          ) : null}
        </div>
  );
};

export default RoomModal;